// ====================================
// HEADER LAYOUT
// ====================================
// Main header section with logo, background overlay, and centered content box
// Classes: .header (container), .header__logo (logo), .header__box (centered content)

.header
    position: relative
    min-height: 95vh
    background: linear-gradient(90deg, rgba($color-brown-light, 0.7) 0.03%, rgba($color-black, 0.7) 99.94%), url(../img/header-background.jpg) center / cover no-repeat
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 80%)

    @media (min-resolution: 192dpi) and (min-width: 670px), (min-width: 1340px)
        background: linear-gradient(90deg, rgba($color-black, 0.7) 0.03%, rgba($color-white, 0.7) 99.94%), url(../img/header-background-big.jpg) center / cover no-repeat

    &__logo
        position: absolute
        top: 5rem
        left: 5rem
        height: 7rem
        widht: 5.6rem

    &__box
        position: absolute
        top: 40%
        left: 50%
        transform: translate(-50%, -50%)
        width: 100%
        text-align: center

        @include respond(tab-land)
            top: 50%
